/* whirl shadow - makes use of box shadowing effects */
.whirl.shadow:after {
  -webkit-animation: spin-shadow 0.25s infinite linear;
  animation: spin-shadow 0.25s infinite linear;
  border-radius: 100%;
  border: none;
}
